.App {
    text-align: center;
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    align-items: center;
    font-size: calc(10px + 2vmin);
    justify-content: center;
}


.counter {
    display:inline-block;
    justify-content: center;
    margin: 50px;
    min-height: 350px;
}

.block {
    height: 150px;

}

.window {
    margin: 0;
    padding: 20px;
    border: solid 4px;
    border-radius: 10px;
    color: #00617e;
}

.buttons-block {
    margin-top: 20px;
    padding: 20px;
    border: solid 4px;
    border-radius: 10px;
}



